<template>
	<view class="container">
		<!-- 头部 -->
		<view class="header">
			<header-navigation @menuChange="menuClick" :tabFlag="true" :languageTxt="Language">
				<template #bg>
					<image class="back-img" src="../../static/Banner4.png"></image>
				</template>
				<template #titleName v-if="Object.keys(Language)!=''">{{Language.tab_Top_store.name}}</template>
				<template #tabName1 v-if="Object.keys(Language)!=''">{{Language.tab_rank_sales.name}}</template>
				<template #tabName2 v-if="Object.keys(Language)!=''">{{Language.tab_rank_cb.name}}</template>
			</header-navigation>
		</view>
		<!-- 头部 end -->
		<!-- 列表 -->
		<view class="list-box" v-if="data.length>0">
			<view class="list" v-for="(item,index) in data" :key="index">
				<view class="list-top disp-flex align-items">
					<view v-if="index>2" class="top-icon font">{{index+1}}</view>
					<image v-if="index==0" src="../../static/ICN_No.1@2x.png" class="top-iconImg"></image>
					<image v-if="index==1" src="../../static/ICN_No.2@2x.png" class="top-iconImg"></image>
					<image v-if="index==2" src="../../static/ICN_No.3@2x.png" class="top-iconImg"></image>
					<view>
						<view class="top-title disp-flex align-items">
							<view class="list-title ellipsis">{{item.store_name}}</view>
							<view class="list-label">{{item.store_code}}</view>
						</view>
						<view class="disp-flex align-items">
							<view class="progress">
								<view :style="{width:item.sales_rate+'%'}" class="progress-bar"></view>
							</view>
							<view class="progress-num">{{item.sales_rate}}%</view>
						</view>
					</view>
				</view>
				<view class="list-border"></view>
				<view class="list-bottom disp-flex align-items disp-justifyAround">
					<view class="comm-bottom-item1">
						<view class="comm-bottom-name color9">Net Revenue</view>
						<view class="comm-bottom-num font colorEF4034">{{item.net_revenue}}</view>
					</view>
					<view class="comm-bottom-line"></view>
					<view class="comm-bottom-item2">
						<view class="comm-bottom-item2-marg disp-flex align-items">
							<view class="comm-bottom-name color9">AVT</view><view class="comm-bottom-num font">{{item.vat}}</view>
						</view>
						<view class="disp-flex align-items">
							<view class="comm-bottom-name color9">UPT</view><view class="comm-bottom-num font">{{item.upt}}</view>
						</view>
					</view>
					<view class="comm-bottom-item2">
						<view class="comm-bottom-item2-marg disp-flex align-items">
							<view class="comm-bottom-name color9">CR</view><view class="comm-bottom-num font">{{item.CR}}</view>
						</view>
						<view class="disp-flex align-items">
							<view class="comm-bottom-name color9">Trans</view><view class="comm-bottom-num font">{{item.trans}}</view>
						</view>
					</view>
				</view>
			</view>
		</view>
		<!-- 列表 end -->
		<!-- 暂无数据 -->
		<view class="empty" v-else>暂无数据</view>
		<!-- 查看更多 -->
		<!-- <uni-load-more :status="status" :content-text="contentText"></uni-load-more> -->
	</view>
</template>

<script>
	const APP=getApp();
	import headerNavigation from "@/components/header/header.vue"
	import {formatTime} from "@/utils/utils.js";
	import {
		getStoreRankingList,
		getStoreRankingListByRate
	} from '../../utils/http.js';
	import {getLanguageData} from "@/utils/utils.js";
	export default{
		components:{headerNavigation},
		data(){
			return{
				data:[],
				tabIndex:0,//标签页销售排行，业绩达成排行tab下标
				menuIndex:0,//当前菜单下标，本日，...
				selectData:'',//选择时间的当前文字
				status:"more",//more:加载前，loading：加载中，noMore：没有数据了
				contentText: {
					// contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				timeType:"D",
				timeValue:"",
				org0:"",
				org1:"",
				org2:"",
				org3:"",
				org4:"",
				Language:{}
			}
		},
		onLoad() {
			this.$i18n.locale = APP.globalData.code;
			this.selectData=this.$t('Other-months');
			getLanguageData().then((res)=>{
				this.Language=res;
			})
		},
		onReachBottom(){
			// console.log("分页")
		},
		methods:{
			// 当前选中的菜单
			menuClick(data){
				this.timeValue=data.timeValue ||formatTime(new Date);
				this.timeType=data.calendarData || "D";
				this.menuIndex=data.menuIndex;
				this.tabIndex=data.tabIndex;
				this.org0=data.lvl0Select;
				this.org1=data.lvl1Select;
				this.org2=data.lvl2Select;
				this.org3=data.lvl3Select;
				this.org4=data.lvl4Select;
				if(data.tabIndex === 0){
					this.getCustomStoreRankingList();
				}else{
					this.getCustomStoreRankingListByRate();
				}
			},
			getCustomStoreRankingList(){
				let that = this;
				that.$showLoading()
				getStoreRankingList({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0:that.org0,
					org1:that.org1,
					org2:that.org2,
					org3:that.org3,
					org4:that.org4
				}).then((res)=>{
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.data = [];
					}else{
						that.data = res;
					}
				})
			},
			getCustomStoreRankingListByRate(){
				let that = this;
				that.$showLoading()
				getStoreRankingListByRate({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0:that.org0,
					org1:that.org1,
					org2:that.org2,
					org3:that.org3,
					org4:that.org4
				}).then((res)=>{
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.data = [];
					}else{
						that.data = res;
					}
					
				})
			}
		}
	}
</script>

<style scoped lang="less">
@import url('@/style/rank.css');
.list-bottom{
	padding-top: 35rpx;
	margin: 0px;
	.comm-bottom-item1{
		.comm-bottom-name{
			font-size: 24rpx;
			font-weight: 400;
			line-height: 24rpx;
			margin-bottom: 17rpx;
		}
		.comm-bottom-num{
			font-size: 32rpx;
			line-height: 36rpx;
		}
	}
	.comm-bottom-line{
		width: 1rpx;
		height: 74rpx;
		background: #EDEDED;
	}
	.comm-bottom-item2{
		font-size: 24rpx;
		line-height: 24rpx;
		.comm-bottom-item2-marg{margin-bottom: 25rpx;}
		.comm-bottom-name{
			width: 90rpx;
			margin-right: 15rpx;
			text-align: right;
		}
		.comm-bottom-num{
			color: #212121;
		}
	}
}
</style>